.animated-text {
    font-family: monospace;
    overflow: hidden; /* hide text that overflows the container */
    border-right: 0.05em solid rgb(8, 8, 8); /* styling for the typing bar */
    white-space: nowrap; /* prevent text from wrapping to a new line */
    -webkit-animation: typing 4s steps(30, end) infinite;
            animation: typing 4s steps(30, end) infinite; /* animation for typing */
  }
  
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 90% }
  }
  
  @keyframes typing {
    from { width: 0 }
    to { width: 90% }
  }
  

