.navbar-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    padding: 0 20px;
    background-color: #fff;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2);
    z-index: 99;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.navbar-brand {
    font-size: 1.3rem;
    font-weight: 600;
    font-family:monospace;
}

.navbar-menu-icon {
    display: none;
    cursor: pointer;
}

.navbar-menu {
    display: flex;
    align-items: center;
    position: absolute;
    top: 100%;
    right: 0;
    background-color: #fff;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2);
    padding: 10px;
    border-radius: 10px;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: all 0.3s ease-in-out;
    list-style: none;
}

.navbar-menu.open {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.navbar-menu-item {
    margin: 0 10px;
}

.navbar-menu-item a {
    font-size: 1.1rem;
    font-family: monospace;
    font-weight: light;
    text-decoration: none;
    color: #333;
}

.navbar-menu-item:hover {
    font-weight: bold;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


.dropdown-menu {
    display: none;
    position: absolute;
    right: 5%;
    z-index: 1;
    background-color: white;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  }
  
  .dropdown-menu-item a {
    font-family: monospace;
    font-size: 1rem;
    text-decoration: none;
    font-weight: lighter;
    color: #333;
    padding: 5px 20px;
    display: block;
  }
/*   
  .dropdown-menu.open {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  } */

  .dropdown:hover .dropdown-menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
  }
  
  .dropdown-menu-item a:hover {
    font-weight: bold;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}



@media (max-width: 768px) {
    .navbar-container {
        z-index: 99;
        padding: 0 10px;
        position: -webkit-sticky;
        position: sticky;
        top: 0;
    }
    .navbar-menu-icon {
        display: block;
        position: absolute;
        right: 1vh;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
    }
    .navbar-menu {
        position: absolute;
        right: 0;
        left: 0;
        height: auto;
        width: 100%;
        flex-wrap: wrap;
        
        flex-direction: column;
        margin-right: 30%;
        width: 100%;
        background-color: #fff;
        box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2);
        padding: 10px;
        border-radius: 10px;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        transition: all 0.3s ease-in-out;
        list-style: none;
        z-index: 2;
    }

    .navbar-menu-item {
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        border-bottom: 1px solid gray;
    }
    .navbar-menu-item a{
        color: gray;
    }

    .navbar-menu-item a:hover {
        color: black;
    }

    .navbar-menu.open {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }


    .dropdown-menu{
        flex-wrap: wrap;
        justify-content: center;
        width: 100%;
        top: 1.4rem;
        right: 0;
        left: 0;
    }
     
  .dropdown-menu.open {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  } 
}




